import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';

const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://i5l4f9dzoc.execute-api.ap-northeast-2.amazonaws.com/rfm_heatmap/rfm-heatmap";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);
    const [data_2, setData_2] = useState([]);
    const [data_3, setData_3] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data[0]));
    }, []);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData_2(result.data[1]));
    }, []);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData_3(result.data[2]));
    }, []);


    // console.log(data)
    // console.log(data_2)

    var data2 = data[0]
    var keylist = [];

    for(var i in data2){
        keylist.push(i)
    }
    // console.log(keylist)

    var j = 0;
    var arrApp = '';
    var arrApp2 = '';
    var arrApp3 = '';
    var arrFinal = [];
    var arrFinal_2 = [];
    var arrFinal_3 = [];   
    while(j<=3){
        arrApp = (new Function ("return"+"arrApp_"+ j));
        arrApp2 = (new Function ("return"+"arrApp2_"+ j));
        arrApp3 = (new Function ("return"+"arrApp3_"+ j))
        var arrApp = Array.from(data, v=>v[keylist[j]])
        var arrApp2 = Array.from(data_2, v=>v[keylist[j]])
        var arrApp3 = Array.from(data_3, v=>v[keylist[j]])

        arrFinal.push(arrApp)
        arrFinal_2.push(arrApp2)
        arrFinal_3.push(arrApp3)
        j = j+1;
        
    }
    // console.log(arrFinal)

    var data2 = [];
    var data_RM = [];
    var data_FM = [];
    var k=0;
    for(var step=0; step<5; step++){
        for(var n in arrFinal[step]){
            var zero = [k, arrFinal.length- Number(n)-1 ,arrFinal[step][n]]
            var zero_2 = [k, arrFinal_2.length- Number(n)-1 ,arrFinal_2[step][n]]
            var zero_3 = [k, arrFinal_3.length- Number(n)-1 ,arrFinal_3[step][n]]
            data2.push(zero)
            data_RM.push(zero_2)
            data_FM.push(zero_3)
        }
        k+=1
    }
    // console.log(data2)

    return (
        <div>
        <h1>R x F</h1>
          <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : 'R과 F에 따른 고객 비율'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '50%',
	            y: '30%',
            },
            xAxis : {
                type : 'category',
                name : 'F-score',
                data : keylist,
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : 'R-score',
                data : [1,2,3,4].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min:0,
                max:17,
                inRange : {   
                    color: ['#3873f2', '#f7f563' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'horizontal',
                left :'center',
                bottom : '70%'
            },
            series : [{
                name : 'R x F 고객비율',
                type : 'heatmap',
                data : data2,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
        <h1>R x M</h1>
        <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : 'R과 M에 따른 고객 비율'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '50%',
	            y: '30%',
            },
            xAxis : {
                type : 'category',
                name : 'M-score',
                data : keylist,
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : 'R-score',
                data : [1,2,3,4].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min:0,
                max:14,
                inRange : {   
                    color: ['#3873f2', '#f7f563' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'horizontal',
                left :'center',
                bottom : '70%'
            },
            series : [{
                name : 'R x M 고객비율',
                type : 'heatmap',
                data : data_RM,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
        <h1>F x M</h1>
        <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : 'F과 M에 따른 고객 비율'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '50%',
	            y: '30%',
            },
            xAxis : {
                type : 'category',
                name : 'M-score',
                data : keylist,
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : 'F-score',
                data : [1,2,3,4].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min:0,
                max:18,
                inRange : {   
                    color: ['#3873f2', '#f7f563' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'horizontal',
                left :'center',
                bottom : '70%'
            },
            series : [{
                name : 'F x M 고객비율',
                type : 'heatmap',
                data : data_FM,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
        </div>
      );
}