import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';



const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://hgwhef09uk.execute-api.ap-northeast-2.amazonaws.com/rfm_classifier/rfm-classifier";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data));
    }, []);

    var arrClass = Array.from( data, v=> v.rfm_customer);
    var arrCount = Array.from(data, v=> v.count_c);
    var arrPR = Array.from(data, v=> v.purchase_period) 

    console.log(arrCount[0])

    var arrCount2 = [];


    return (
        <div>
        <h1> 고객 구분 별 고객 수</h1>
          <ReactEcharts theme = {"dark"} option = {{
            title: {text:'고객 구분 별 고객 수'},
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                type : 'cross',
                crossStyle : {
                  color : '#999'
                  }
                }},
            toolbox: {feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '15%',
	            left: '5%',
	            right: '10%',
	            bottom: '5%',
	            containLabel: true,
            },
            legend: {
                data : ['고객 구분별 고객 수']
            },
            xAxis: {
                data : arrClass,
                name : '고객 구분',
                axisPointer : {
                        type : 'shadow'
                        }
            },
            yAxis: [{
                type : 'value',
                name : '고객 구분별 고객 수',
                axisLabel : {
                        formatter:'{value}'
                        }
            }],
            series: [{
                name:'고객 구분별 고객 수',
                type: 'bar',
                data: arrCount2.concat(arrCount.slice(0,-7) ,{value: Number(arrCount.slice(1,-6)), 
                    itemStyle : {color: '#f52a2a'}} ,{value: Number(arrCount.slice(2,-5)), 
                        itemStyle : {color: '#fcf74e'}},arrCount.slice(3,5), {value: Number(arrCount.slice(6,-1)), 
                            itemStyle : {color: '#fcf74e'}},arrCount.slice(6,-1), {value: Number(arrCount.slice(-1)), 
                                itemStyle : {color: '#fcf74e'}}),
                label: {
                    show: true,
                    position: 'top'
                }
            }]
          }} />
          <h1> 고객 구분 별 구매 주기</h1>
          <ReactEcharts theme = {"dark"} option = {{
            title: {text:'고객 구분 별 구매 주기'},
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                type : 'cross',
                crossStyle : {
                  color : '#999'
                  }
                }},
            toolbox: {feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '15%',
	            left: '5%',
	            right: '10%',
	            bottom: '5%',
	            containLabel: true,
            },
            legend: {
                data : ['고객 구분별 구매 주기']
            },
            xAxis: {
                type : 'value',
                name : '구매 주기',
                boundaryGap : [0, 0.0],
                axisPointer : {
                        type : 'shadow'
                        },
                inverse : true
            },
            yAxis: [{
                type : 'category',
                data : arrClass,
                name : '고객 구분'
            }],
            series: [{
                name:'고객 구분별 고객 주기',
                type: 'bar',
                data: [
                    {
                        value: arrPR[0],
                        itemStyle: {color: '#f56262'},
                    },{
                        value: arrPR[1],
                        itemStyle: {color: '#f29446'},
                    },{
                        value: arrPR[2],
                        itemStyle: {color: '#f5f05f'},
                    },{
                        value: arrPR[3],
                        itemStyle: {color: '#68f558'},
                    },{
                        value: arrPR[4],
                        itemStyle: {color: '#67f5eb'},
                    },{
                        value: arrPR[5],
                        itemStyle: {color: '#6a99eb'},
                    },{
                        value: arrPR[6],
                        itemStyle: {color: '#ae78f5'},
                    },{
                        value: arrPR[7],
                        itemStyle: {color: '#f567e0'},
                    }
                ],
                label: {
                    show: true,
                    position: 'right'
                }
            }]
          }} />
        </div>
      );
}

