import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';



const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://ia0d7cnox3.execute-api.ap-northeast-2.amazonaws.com/rfm_freq/rfm-freq";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data));
    }, []);

    var arrFRQ = Array.from( data, v=> v.frequency);
    var arrCount = Array.from(data, v=> v.customer_id);

    // console.log(arrCount[0])

    return (
        <div>
        <h1> 주문 별 고객 수</h1>
          <ReactEcharts theme = {"dark"} option = {{
            title: {text:'주문 별 고객 수'},
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                type : 'cross',
                crossStyle : {
                  color : '#999'
                  }
                }},
            toolbox: {feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '15%',
	            left: '5%',
	            right: '10%',
	            bottom: '5%',
	            containLabel: true,
            },
            legend: {
                data : ['주문 별 고객 수']
            },
            xAxis: {
                data : arrFRQ,
                name : '주문 수(Frequency)',
                axisPointer : {
                        type : 'shadow'
                        }
            },
            yAxis: [{
                type : 'value',
                name : '주문 별 고객 수',
                axisLabel : {
                        formatter:'{value}'
                        }
            }],
            series: [{
                name:'주문 별 고객 수',
                type: 'bar',
                data: arrCount,
                label: {
                    show: true,
                    position: 'top'
                }
            }]
          }} />
        </div>
      );
}

