import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';



const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://p2yn7tplgd.execute-api.ap-northeast-2.amazonaws.com/v2/query-to-api2";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data));
    }, []);

    // var arrDate = Array.from(data, v=> v.orderdate);
    // var arrApp_1 = Array.from(data, v=> v.App_1);
    // var arrApp_2 = Array.from(data, v=> v.App_2);

    // console.log(data)
    var data2 = data[0]
    var keylist = [];
    for(var i in data2){
        keylist.push(i)
    }
    // console.log(data[0].length)
    console.log(keylist.length)


    var j = 0;
    var arrApp = '';
    var arrFinal = [];
    var series_option = [];
    while(j<=88){
        arrApp = (new Function ("return"+"arrApp_"+ j));

        // var arrApp = Array.from(data, v=>v.App_1)
        var arrApp = Array.from(data, v=>v[keylist[j]])
        // console.log(arrApp)
        arrFinal.push(arrApp)
        j = j+1;
        
    }
    // console.log(arrFinal.length)

    var k = 1;
    while(k<=(arrFinal.length-1)){
        var series1 = {
            name : keylist[k],
            type : 'bar',
            stack : 'total',
            label : {
                show : false
            },
            emphasis : {
                focus : 'series'
            },
            data : arrFinal[k]
        } 
        series_option.push(series1)
        k=k+1;
    }
    // console.log(series_option)

    
    return (
        <div>
          <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : '누적 구매횟수와 매출합'
            },
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                    type : 'cross',
                    crossStyle : {
                        color : '#999'
                    }
                }
            },
            toolbox: {
	            feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '15%',
	            left: '5%',
	            right: '10%',
	            bottom: '5%',
	            containLabel: true,
            },
            legend : {
                data : keylist
            },
            xAxis : {
                data : arrFinal[0],
                name : '년-월',
                axisPointer : {
                        type : 'shadow'
                        }
                },
            yAxis : [{
                type : 'value',
                name : '구매금액합계',
                axisLabel : {
                        formatter:'{value}'
                        }
            }],
            series : series_option
          }} />
        </div>
      );
}