
import React ,{Component} from 'react';
import ReactEcharts from 'echarts-for-react';
import axios from 'axios';

// const axios = require('axios')
const proxyurl = "https://cors-anywhere.herokuapp.com/";
const url = "https://kc0201wkyb.execute-api.ap-northeast-2.amazonaws.com/v1/hello";

// axios.get(proxyurl+url)
// .then((Response)=>{console.log(Response.data)})
// .catch((Error)=>{console.log(Error)})

class Bar extends Component{
    constructor(props){
        super(props)
        this.state = {
            // orderDate : props.orderDate,
            // sales: props.sum,
            // stores: props.count
            sales:[5, 20, 36, 10, 10, 20],
            stores:[15, 120, 36, 110, 110, 20]
        }
    }
    /**
           * Configuration object of histogram
     */
    getOption = (sales,stores) =>{
        return {
            title: {
                text:'ECharts introduction example'
            },
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                    type : 'cross',
                    crossStyle : {
                        color : '#999'
                    }
                }
            },
            toolbox: {
	            feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            legend: {
                data: ['Sales','Inventory']
            },
            xAxis: {
                data: ["Shirt", "Cardigan", "Chiffon Shirt", "Pants", "High Heels", "Socks"]
            },
            yAxis: [{
                type : 'value',
                name : '구매금액합계(월별)',
                axisLabel : {
                        formatter:'{value}'
                        }
            },
            {
                type : 'value',
                name : '구매건수합계(월별)',
                offset : 0,
                axisLabel : {
                        formatter : '{value}'
                        }
            }],
            series: [{
                name:'Sales',
                type: 'bar',
                data: sales
            },
            {
                name:'Inventory',
                type: 'line',
                yAxisIndex : 1,
                data: stores
            }]
        };
    }

    // componentWillMount() {
    //     const proxyurl = "https://cors-anywhere.herokuapp.com/";
    //     const url = "https://kc0201wkyb.execute-api.ap-northeast-2.amazonaws.com/v1/hello";
    //     fetch(proxyurl + url)
    //         .then(res => res.json())
    //         .then(data => console.log(data));
    // }
    


    render(){
        const {sales,stores} = this.state;
        return(
            <div>
                <ReactEcharts option={this.getOption(sales,stores)} />
            </div>
        );
    }
}
export default Bar;