import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';

const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://y0xy30q5e7.execute-api.ap-northeast-2.amazonaws.com/v4/query_to_api4";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data));
    }, []);

    // console.log(data)
    var data2 = data[0]
    var keylist = [];

    for(var i in data2){
        keylist.push(i)
    }
    // console.log(keylist)

    var j = 0;
    var arrApp = '';
    var arrFinal = [];
    while(j<=16){
        arrApp = (new Function ("return"+"arrApp_"+ j));

        var arrApp = Array.from(data, v=>v[keylist[j]])
        arrFinal.push(arrApp)
        j = j+1;
        
    }
    // console.log(arrFinal)

    var data2 = [];
    var k=0;
    for(var step=1; step<17; step++){
        for(var n in arrFinal[step]){
            var zero = [k, arrFinal[step].length - Number(n)-1 ,arrFinal[step][n]]
            data2.push(zero)
        }
        k+=1
    }
    // console.log(data2)

    return (
        <div>
          <ReactEcharts theme = {"dark"} option = {{
            title : {
                text : '코호트 재구매율'
            },
            tooltip : {
                position :'top'
            },
            animation : false,
            toolbox: {
	            feature: {
		            dataView: {show: false, readOnly: false},
		            magicType: {show: false, type: ['line', 'bar']},
		            restore: {show: false},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            height: '70%',
	            y: '15%',
            },
            xAxis : {
                type : 'category',
                name : '차이',
                data : keylist.slice(1),
                splitArea : {
                        show : true
                        }
                },
            yAxis : {
                type : 'category',
                name : '첫구매일자',
                data : arrFinal[0].reverse(),
                splitArea : {
                    show: true
                        }
            },
            visualMap : {
                min:0,
                max:100,
                inRange : {   
                    color: ['#3873f2', '#f7f563' ] //From smaller to bigger value ->
                },
                calculable : true,
                orient : 'vertical',
                left :'right',
                bottom : '35%'
            },
            series : [{
                name : 'Cohort',
                type : 'heatmap',
                data : data2,
                label : {
                    normal :{
                        show : true
                    } 
            },
            itemStyle : {
                emphasis: {
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        }} />
        </div>
      );
}