import React, { Component } from 'react';

class Student extends Component 
{
    state={
        txtname:'',
        txtage:'',
        txtresult:'show'
    }


    nameChange = (e) =>{
        this.setState( { txtname:e.target.value } )
    }
    ageChange = (e) =>{
        this.setState( { txtage:e.target.value } )
    }

    btnClick = () =>{
        var {txtname, txtage} = this.state
        this.setState( {txtresult: `name:${txtname} age:${txtage}` } )
    }

  render() 
  {
    return (
      <div>
            <h1>Student</h1>
            <div>name:</div>
            <input type="text" value={this.state.txtname} onChange={this.nameChange} /><br/><br/>
            <div>age:</div>
            <input type="number" value={this.state.txtage} onChange={this.ageChange} /><br/>
            <div>{this.state.txtresult}</div>
            <button onClick={this.btnClick}>click</button>
      </div>
    );
  }
}

export default Student;
