import React, { Component } from 'react';
import a from './image/a.jpg'
import b from './image/b.jpg'

class MyEvent extends Component 
{
    state={
        aa:'aaa',
        aimg: a
    }
    inputChange = (e) =>{
        this.setState( { aa:e.target.value } )
    }
    btnClick = ()=>{
        this.myh.innerText ="hi..."
        this.myimg.src = b
        //this.setState( { aimg:b } )
        this.myinput.focus()
    }
  render() {
    return (
      <div>
            <h1>MyEvent</h1>
            <h1 ref={(ref)=>this.myh=ref}>test..</h1>
            <img ref={(ref)=>this.myimg=ref}  src={this.state.aimg}/>
            <input type="text" value={this.state.aa} onChange={this.inputChange} />
            <input type="text" ref={(ref)=>this.myinput=ref} />
            <button onClick={this.btnClick}>click</button>
      </div>
    );
  }
}

export default MyEvent;
