import React, {useEffect, useState} from "react";
import axios from "axios";
import ReactEcharts from 'echarts-for-react';



const proxyurl = "https://api.allorigins.win/raw?url= "
const url = "https://7i0mi3qtaj.execute-api.ap-northeast-2.amazonaws.com/v1/query-to-api1";

export default function RestapihooksComponent() {

    const [data, setData] = useState([]);

    useEffect(() => {
        axios
        .get(proxyurl+url)
        .then(result => setData(result.data));
    }, []);

    var arrDate = Array.from( data, v=> v.orderdate);
    var arrSum = Array.from(data, v=> v.sum_s);
    var arrCount = Array.from(data, v=> v.count_s) 

    return (
        <div>
          <ReactEcharts theme = {"dark"} option = {{
            title: {text:'월별 구매량과 구매건수'},
            tooltip : {
                trigger : 'axis',
                axisPointer : {
                type : 'cross',
                crossStyle : {
                  color : '#999'
                  }
                }},
            toolbox: {feature: {
		            dataView: {show: true, readOnly: false},
		            magicType: {show: true, type: ['line', 'bar']},
		            restore: {show: true},
		            saveAsImage: {show: true}
	            }
            },
            grid: {
	            top: '15%',
	            left: '5%',
	            right: '10%',
	            bottom: '5%',
	            containLabel: true,
            },
            legend: {
                data : ['구매금액합계(월별)','구매건수합계(월별)']
            },
            xAxis: {
                data : arrDate,
                name : '년-월',
                axisPointer : {
                        type : 'shadow'
                        }
            },
            yAxis: [{
                type : 'value',
                name : '구매금액합계(월별)',
                axisLabel : {
                        formatter:'{value}'
                        }
            },
            {
                type : 'value',
                name : '구매건수합계(월별)',
                offset : 0,
                axisLabel : {
                        formatter : '{value}'
                        }
            }],
            series: [{
                name:'구매금액합계(월별)',
                type: 'bar',
                data: arrSum
            },
            {
                name:'구매건수합계(월별)',
                type: 'line',
                yAxisIndex : 1,
                itemStyle: {
			            normal: {
				            color: '#fbff14'
			                    }
                },
                lineStyle : {
                    color : '#fbff14',
                    width : 5 
                },
                symbolSize: 7,
                data: arrCount
            }]
          }} />
        </div>
      );
}