import React, { Component } from 'react';

class MyC extends Component 
{
    state=
    {
        n:0,
        s:'aaa'
    }
    btnClick = ()=>
    {
        this.setState( {n: this.state.n+1, s:'bbb'})
    }
  render() 
  {
    return (
            <div>
                <h1>MyC...</h1>
                <h2>{this.props.name}</h2>
                <h2>{this.props.age}</h2>
                <h2>{this.state.n}</h2>
                <h2>{this.state.s}</h2>
                <button onClick={ ()=>{
                    this.setState( {n: this.state.n+1, s:'bbb'})
                } }>click</button>
                <button onClick={this.btnClick}>click...</button>
            </div>
    );
  }
}

export default MyC;